.App {
  height: 10vh;

}
.navbar{
  margin: 0;
  font-size: 18px;
  border:0;
  border-radius: 0;

}

.App-logo {
  -webkit-animation: App-logo-spin infinite 6s linear;
          animation: App-logo-spin infinite 6s linear;
  height: 100px;
  padding-right: 1em;

}

/* .App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
} */

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.link {
  color: white;
  text-decoration:none;

}
.link:active {
  color: white; 
  text-decoration:none;
}

.link:hover{
  color: white;
  text-decoration:none;
}

.link:visited{
  color: white;
  text-decoration:none;
}

.link:link{
  text-decoration:none;
}

.navbar-end .item:hover{
  background-color: grey;
}

.deconnexion:hover{
  background-color: none;
}

@-webkit-keyframes App-logo-spin {
  0% { -webkit-transform: rotateZ(0); transform: rotateZ(0) } 
  1% {  -webkit-transform: rotateZ(-20deg);  transform: rotateZ(-20deg); }
  2% { -webkit-transform: rotateZ(20deg); transform: rotateZ(20deg) } 
  3% {  -webkit-transform: rotateZ(-20deg);  transform: rotateZ(-20deg); }
  4% { -webkit-transform: rotateZ(20deg); transform: rotateZ(20deg) } 
  5% {  -webkit-transform: rotateZ(-20deg);  transform: rotateZ(-20deg); }
	6% {  -webkit-transform: rotateZ(0);  transform: rotateZ(0); } 
	100% {  -webkit-transform: rotateZ(0);  transform: rotateZ(0);} 
}

@keyframes App-logo-spin {
  0% { -webkit-transform: rotateZ(0); transform: rotateZ(0) } 
  1% {  -webkit-transform: rotateZ(-20deg);  transform: rotateZ(-20deg); }
  2% { -webkit-transform: rotateZ(20deg); transform: rotateZ(20deg) } 
  3% {  -webkit-transform: rotateZ(-20deg);  transform: rotateZ(-20deg); }
  4% { -webkit-transform: rotateZ(20deg); transform: rotateZ(20deg) } 
  5% {  -webkit-transform: rotateZ(-20deg);  transform: rotateZ(-20deg); }
	6% {  -webkit-transform: rotateZ(0);  transform: rotateZ(0); } 
	100% {  -webkit-transform: rotateZ(0);  transform: rotateZ(0);} 
}
