.cbutton {
    margin-top: 20px;
  display: inline-block;
  position: relative;
  padding: 7px 7px;
  top: 0;
  font-size: 20px;
  font-family: "Open Sans", Helvetica;
  border-radius: 4px;
  border-color: rgb(58, 157, 238);
  border-bottom: 1px solid rgba(21, 38, 44, 0.5);
  background: rgb(58, 157, 238);
  color: #fff;
  -webkit-box-shadow: 0px 0px 0px rgba(15, 130, 165, 0.1);
          box-shadow: 0px 0px 0px rgba(15, 130, 165, 0.1);
  
  -webkit-transform: translateZ(0);
      -ms-transform: translateZ(0);
          transform: translateZ(0);
  
  -webkit-transition: all 0.2s ease;
          -o-transition: all 0.2s ease;
          transition: all 0.2s ease;
}

.cbutton:hover {
  top: -10px;
  -webkit-box-shadow: 0px 10px 10px rgba(15, 122, 165, 0.2);
          box-shadow: 0px 10px 10px rgba(15, 122, 165, 0.2);
  
  -webkit-transform: rotateX(5deg);
      -ms-transform: rotateX(5deg);
          transform: rotateX(5deg);
}

.cbutton:active {
  top: 0px;
  -webkit-box-shadow: 0px 0px 0px rgba(15, 135, 165, 0);
          box-shadow: 0px 0px 0px rgba(15, 135, 165, 0);
  background: rgb(58, 157, 238);
}





