.img {
    background-image: url(https://images.unsplash.com/photo-1460925895917-afdab827c52f?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=5a7430d7bd5676bc7b81f2b8bf8f6a75&auto=format&fit=crop&w=1902&q=80);
    background-size: cover;
    background-position:center center;

    height: calc(100vh - 52px);
    position: relative;
}

.pingit {
    position: fixed;
    top: 200px;
    width: 110%;
    left: -10px;
    height:29vh;
    background-color: rgba(0, 0, 0, 0.404);
    text-align: center;
    vertical-align: middle;
}


.main-text {
    color: rgb(255, 255, 255);
    font-size: 10vh;
}

.link-text {
    font-size: 2vh;
    color: white;

    border: solid;
    border-radius: 3px;
    padding: 1vh;

    position: absolute;
    top: 20vh;
    left: 40%;
    -webkit-transform: translate(-50%);
        -ms-transform: translate(-50%);
            transform: translate(-50%);
}

.link-text:hover{
    color: rgb(0,169,251);
  }

.logo {
  -webkit-animation: App-logo-spin infinite 6s linear;
          animation: App-logo-spin infinite 6s linear;
  height: 10vh;
  padding-right: 1vh;
  position: relative;
  top: 1vh
  


}
.agent{
    font-size: 2vh;
    color: white;

    border: solid;
    border-radius: 3px;
    padding: 1vh;

    position: absolute;
    top: 20vh;
    left: 60%;
    -webkit-transform: translate(-50%);
        -ms-transform: translate(-50%);
            transform: translate(-50%);

}


